html::before {
  content: "";
  display: block;
  position: absolute;
  z-index: -1001;
  width: 15em;
  height: 8em;
  top: 0;
  left: 0;
  background: url("/assets/images/shapes.svg") no-repeat;
  background-size: cover;
  transform: rotate(0.5turn);
}

footer::after {
  content: "";
  display: block;
  position: absolute;
  z-index: -1001;
  width: 15em;
  height: 8em;
  bottom: 0;
  right: 0;
  background: url("/assets/images/shapes.svg") no-repeat;
  background-size: cover;
}

h1::after {
  content: "";
  display: block;
  position: absolute;
  z-index: -1000;
  width: 3.6em;
  height: 0.5em;
  margin-left: calc(50% - 1.8em);
  bottom: 0.05em;
  background-color: var(--color-3);
}

footer {
  height: 13em;
}
footer section {
  margin-left: 5em;
  margin-right: 5em;
}

@media (min-width: 600px) {
  footer {
    height: 8em;
  }
}